<template>
  <div class="yan-good-spec">
    <!--  -->
    <div class="info-title">
      
    </div>

    <!--  -->
    <div class="info-wrapper">
      <!--  -->
      <div v-for="(info, index) in info" :key="index" class="info">
        <!--  -->
        <div class="info-attr">{{info.attrName}}</div>
        <!--  -->
        <div class="info-value">{{info.attrValue}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['info']
}
</script>

<style scoped>
.yan-good-spec {
  padding: 35px;
  background: #fff;
}

/*  */
.info-title {
  padding: 45px 0 30px;
  font-size: 38px;
  font-size: 14px; /* bug*/
}

/*  */
.info {
  padding: 15px 35px 15px 0;
  display: flex;
  align-items: center;
  border-top: 1px dashed rgb(153, 153, 153);
}

.info-attr {
  width: 200px;
  color: rgb(153, 153, 153);
}

.info-attr,
.info-attr {
  display: inline-flex;
  line-height: 50px;
}
</style>



