const state = {
    // 
    chatContent: [{
        content: '',
        isMe: false,
        time: new Date().getTime()
    }]
}

const getters = {
  chatContent: state => state.chatContent,
}

const mutations = {
    // 
    setChatContent(state, payload) {
        state.chatContent.push(payload.chatContent);

        let response = '';

        // 
        if (payload.chatContent.content.indexOf('') !== -1) {
            response = '';
        }
        else if (payload.chatContent.content.indexOf('') !== -1) {
            response = '';
        }
        else {
            response = '';
        }
        state.chatContent.push({
            content: response,
            isMe: false,
            time: new Date().getTime()
        });
    },
}

const actions = {

}

export default {
  state,
  getters,
  actions,
  mutations
}



