<template>
  <div class="yan-cart-item">
    <!--  -->
    <div @click="select" class="yan-item-check">
      <i v-show="cartItem.select === 0" class="fa fa-circle-o" aria-hidden="true"></i>
      <i v-show="cartItem.select === 1" class="fa fa-check-circle-o" aria-hidden="true"></i>
    </div>

    <!--  -->
    <div class="yan-item-intro">
      <!--  -->
      <router-link tag="div" :to="`/good-info/${cartItem.ID}`" class="yan-item-pic">
        <img :src="cartItem.picture" alt="cart item">
      </router-link>

      <!--  -->
      <div class="yan-item-spec">
          <!--  -->
          <div class="yan-item-type">
            <span >{{cartItem.type}}</span>
          </div>

          <!--  -->
          <div class="yan-item-num">
            <span>&yen;{{cartItem.price}}</span>

            <yan-edit-num-select :index="index"></yan-edit-num-select>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
// 
import YanEditNumSelect from '@/components/pages/Cart/EditItem/NumSelect/NumSelect';

export default {
  props: ['cartItem', 'index'],
  data() {
    return {
        num: 1
    }
  },
  methods: {
    select() {
      this.$store.commit('selectCart', {
        index: this.index,
        state: this.cartItem.select === 0 ? 1 : 0
      })
    }
  },
  components: {
      YanEditNumSelect
  }
}
</script>

<style scoped>
.yan-cart-item {
  display: flex;
  align-items: center;
  padding: 0 0 0 35px; 
  background: #fff;
}

/*  */
.fa-circle-o {
  color: rgb(200, 200, 200);
  font-size: 50px;
}

.fa-check-circle-o {
  color: rgb(180, 40, 45);
  font-size: 50px;
}

/*  */
.yan-item-intro {
  padding: 30px 0;
  margin-left: 25px;
  display: flex;
  align-items: center;
  flex-grow: 1;
  border-bottom: 1px solid rgb(200, 200, 200);
}

/*  */
.yan-item-pic {
  background: rgb(242, 242, 242);
  border-radius: 8px;
}

.yan-item-pic img {
  width: 180px;
}

/*  */
.yan-item-spec {
  padding: 25px 35px;
  display: inline-flex;
  flex-direction: column;
  flex-grow: 1;
  align-self: flex-start;
}

/*  */
.yan-item-type {
  display: flex;
  flex-direction: column;
  margin-bottom: 45px;
}

.yan-item-type span {
    font-size: 32px;
    color: rgb(153, 153, 153);  
    align-self: flex-end;
}

/*  */
.yan-item-num {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  font-size: 40px;
}
</style>



